import React, {Component} from 'react';
import {connect} from 'react-redux';
import {findMemo} from './Store';

class FindForm extends Component {
  input = {
    fontSize: '16px',
    color: '#006',
    padding: '1px',
    margin: '5px 0px'
  }
  btn = {
    fontSize: '14px',
    color: '#006',
    padding: '2px 10px',
  }

  constructor(props) {
    super(props);
    this.state = {
      find: ''
    }
    this.doChange = this.doChange.bind(this);
    this.doAction = this.doAction.bind(this);
  }

  doChange(e) {
    this.setState({
      find: e.target.value
    });
  }

  doAction(e) {
    e.preventDefault();
    let action = findMemo(this.state.find);
    this.props.dispatch(action);
  }
  
  render() {
    return (
      <div>
        <form onSubmit={this.doAction} >
          <input type='text' onChange={this.doChange} value={this.state.message} style={this.input}/>
          <input type='submit' value='Find' style={this.btn}/>
        </form>
      </div>
    )
  }
}

export default connect((state) => state)(FindForm);